"use client";
import React from 'react';
import Link from 'next/link';
import Button from './Button';

export default function Layout({ children }: { children: React.ReactNode }) {
  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center gap-4">
              <Link href="/" className="text-2xl font-bold text-blue-600">Clinics</Link>
              <p className="text-sm text-gray-500 hidden sm:block">Book medical appointments easily</p>
            </div>

            <nav className="hidden md:flex items-center gap-3">
              <Link href="/clinics" className="text-gray-700 hover:text-gray-900">Clinics</Link>
              <Link href="/doctors" className="text-gray-700 hover:text-gray-900">Doctors</Link>
              <Link href="/about" className="text-gray-700 hover:text-gray-900">About</Link>
              <Link href="/login">
                <Button variant="outline" size="sm">Login</Button>
              </Link>
            </nav>

            <div className="md:hidden">
              <Link href="/login"><Button size="sm">Login</Button></Link>
            </div>
          </div>
        </div>
      </header>

      <main className="flex-1">
        {children}
      </main>

      <footer className="bg-white border-t mt-8">
        <div className="max-w-7xl mx-auto px-4 py-6 text-sm text-gray-600">Â© 2025 Clinics Booking System</div>
      </footer>
    </div>
  );
}
